import {Table} from 'react-bootstrap'
import {useDispatch, useSelector} from 'react-redux'
// import store, {changeName, increase} from './store.js'
import { addCount, sortName } from './store.js'
import { Button } from 'react-bootstrap'

import Col from 'react-bootstrap/Col';
import Form from 'react-bootstrap/Form';
import Row from 'react-bootstrap/Row';
import { MDBBtn} from 'mdb-react-ui-kit';


function Cart(){

    let state = useSelector((state) => state) 
    // console.log(state.cart[0].name);

    // dispatch는  store.js 로 요청보내주는 함수
    let dispatch = useDispatch()
   return(
     <>
       <h5 style={{fontSize:'20px', marginTop:'40px', color:'#333', fontFamily:'Noto Sans KR'}}>{state.user.name}님의 장바구니</h5> 
       {/* <button onClick={()=>{ dispatch(increase())}}>나이+</button>
       <button onClick={()=>{ dispatch(increase(100))}}>나이+</button> */}

       
       <Table style={{width:'60%', margin:'0 auto'}}>
        <thead>
            <tr>
            <th>상품코드</th>
            <th>상품명</th>
            {/* <th>가격</th> */}
            <th>수량</th>
            <th>추가하기</th>
            </tr>
        </thead>
        <tbody>
      
            {
                state.cart.map((a, i)=>
                <tr key={i}>
                    <td>{state.cart[i].id}</td>
                    <td>{state.cart[i].name}</td>
                    {/* <td>{state.cart[i].price}</td> */}
                    <td>{state.cart[i].count}</td>
                    <td><MDBBtn onClick={()=>{
                         dispatch(addCount(state.cart[i].id))
                    }} rippleColor='#c1303a' color='light' variant="outline-success" 
                    style={{width:'30px', height:'30px', backgroundColor:'#eee', padding:'3px 0 0 0px', boxShadow:'none', fontSize:'11px', borderRadius:'20px'}}>+</MDBBtn>
                    </td>
                </tr>
                )
            }

        </tbody>
       </Table> 
       <MDBBtn variant="outline-primary" onClick={()=>{
         dispatch(sortName(state.cart.Name))
       }} rippleColor='#c1303a' color='light'
       style={{width:'85px', height:'30px', backgroundColor:'#eee', padding:'3px 0 0 0px', boxShadow:'none', fontSize:'11px', borderRadius:'20px', marginTop:'20px'}}>이름순정렬</MDBBtn>{' '}




    <Form style={{width:'46%', margin:'40px auto 10px'}}>
      <h3 style={{fontSize:'20px', color:'#333', fontFamily:'Noto Sans KR'}}>주문/결제</h3>
    <Row>
    <h3 style={{fontSize:'16px', color:'#333', textAlign:'left', fontFamily:'Noto Sans KR'}}>배송지 정보</h3>
        <Col>
          <Form.Control placeholder="성" style={{fontSize:'13px'}} />
        </Col>
        <Col>
          <Form.Control placeholder="이름" style={{fontSize:'13px'}} />
        </Col>
      </Row>

      <Row className="mb-3" style={{marginTop:'25px'}}>
        <Form.Group as={Col} controlId="formGridEmail">
          {/* <Form.Label>Email</Form.Label> */}
          <Form.Control type="email" placeholder="이메일" style={{fontSize:'13px'}} />
        </Form.Group>

        <Form.Group as={Col} controlId="formGridPassword">
          {/* <Form.Label>Password</Form.Label> */}
          <Form.Control type="password" placeholder="비밀번호" style={{fontSize:'13px'}} />
        </Form.Group>
      </Row>

      <Form.Group className="mb-3" controlId="formGridAddress1">
        {/* <Form.Label>Address</Form.Label> */}
        <Form.Control placeholder="주소를 입력하세요" style={{fontSize:'13px'}} />
      </Form.Group>

      <Form.Group className="mb-3" controlId="formGridAddress1">
        {/* <Form.Label>Address</Form.Label> */}
        <Form.Control placeholder="배송 시 요청사항을 입력하세요" style={{fontSize:'13px'}} />
      </Form.Group>


      <hr></hr>
      
      <h3 style={{fontSize:'16px', color:'#333', textAlign:'left', fontFamily:'Noto Sans KR'}}>쿠폰 / 포인트</h3>

      <div class="btn-group shadow-0" style={{float:'left'}}>
        <button type="button" class="btn btn-light dropdown-toggle" data-mdb-toggle="dropdown" aria-expanded="false">쿠폰 선택</button>
        <ul class="dropdown-menu">
          <li><a class="dropdown-item" href="#">배송비 무료 쿠폰</a></li>
          <li><a class="dropdown-item" href="#">~ 7.31 15% 할인 쿠폰</a></li>
          <li><a class="dropdown-item" href="#">전 상품 20% 할인 쿠폰</a></li>
        </ul>
      </div>

      <hr style={{marginTop:'60px'}}></hr>


      <Button variant="primary" type="submit" style={{backgroundColor:'black'}}>
        결제하기
      </Button>
    </Form>



     </>

   )

}

export default Cart